import React, { Component } from "react";
import Zmage from "react-zmage";

let id = 0;
class Portfolio extends Component {
  render() {
    if (!this.props.data) return null;

    const projects = this.props.data.projects.map(function(projects) {
      let projectImage = "images/portfolio/" + projects.image;

      return (
        <div key={id++} className="columns portfolio-item" style={{ width: "33%" }}>
          <div className="item-wrap" >
            <Zmage alt={projects.title} src={projectImage} />
            <a style={{ fontSize:"17px",textAlign: "center",color:"#0066FF",marginTop:"15px",marginBottom:"15px" }} href={projects.url} target="_blank">{projects.title}</a>
            {/*<p style={{ textAlign: "center" }} >{projects.category}</p>*/}
          </div>
        </div>
      );
    });

    return (
      <section id="portfolio">
        <div className="row">
          <div className="twelve columns collapsed">
            <h1>Check Out Some of My Works.</h1>

            <div id="portfolio-wrapper" className="bgrid-quarters s-bgrid-thirds cf">
              {projects}
            </div>
          </div>
        </div>
      </section>
    );
  }
}

export default Portfolio;
